import React, { useState } from "react";

function Login({ getName }) {
    const [nombre, setNombre] = useState("");

    const Submit = (e) => {
        e.preventDefault();
        getName(nombre)
    };

    return (
        <div id="nickWrap">
            <div className="row ">
                <div className="col-md-4 mx-auto ">
                    <div className="card">
                        <div className="card-header">
                            <h3>Enter your Username</h3>
                        </div>
                        <p id="nickError"></p>
                        <div className="card-body">
                            <form id="nickForm" onSubmit={Submit}>
                                <input id="nickname" type="text" className="form-control" value={nombre} onChange={(e) => setNombre(e.target.value)} />
                                <input type="submit" className="btn btn-warning btn-block mt-2" />
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Login;
