import React, { useState } from "react";

import socket from './socket'

import Login from './components/login'
import Alert from './components/alert'
import Navbar from './components/navbar'
import Container from './components/container'
import Chat from './components/chat'

function App() {
  const [registrado, setRegistrado] = useState(false);
  const [mensajerror, setMensajerror] = useState("");
  const [users, setUsers] = useState([]);
  const [mensajes, setMensajes] = useState([]);

  socket.on('connection')

  const data = (nombre) => {
    if (nombre !== "") {
      socket.emit('new user', nombre)
      socket.on('response NewUser', data => {
        setRegistrado(data)
        if (data === false) {
          setMensajerror('Este usuario ya esta registrado')
        }
      })
    }
  }

  const usuarios = () => {
    socket.on('usernames', data => {
      setUsers(data)
    })
    return users
  }

  const getMensajes = () => {
    socket.on('load old msgs', data => {
      setMensajes(data)
    })
    return mensajes
  }
  const Setmensajes = (mensaje) => {
    socket.emit('send message',mensaje)
  }

  return (
    <>
      <Navbar>Chat en tiempo real</Navbar>

      {mensajerror !== "" ?
        <Alert color="danger">{mensajerror}</Alert>
        : ''
      }
      <Container>
        {registrado ?
          <Chat getUsers={usuarios} getMenssages={getMensajes}  SetMensaje={Setmensajes}/> :
          <Login getName={data} />
        }
      </Container>

    </>
  );
}

export default App;
